@mixin placeholder {
  &::-webkit-input-placeholder {@content}
  &:-moz-placeholder           {@content}
  &::-moz-placeholder          {@content}
  &:-ms-input-placeholder      {@content}  
} 
@mixin opacity ( $value ) {
  opacity: $value; 
} 
@mixin border-radius($tl, $tr, $br, $bl) {
  border-radius: $tl $tr $br $bl;
  -moz-border-radius: $tl $tr $br $bl;
  -webkit-border-radius: $tl $tr $br $bl;
  -ms-border-radius: $tl $tr $br $bl;
} 
@mixin ab($t, $l, $b, $r) {
  position: absolute;
  display: block;
  @if $l != n { left: $l; }
  @if $t != n { top: $t; }
  @if $r != n { right: $r; }
  @if $b != n { bottom: $b; }
}
@mixin transition($transition...) {
  -moz-transition:    $transition;
  -o-transition:      $transition;
  -webkit-transition: $transition;
  transition:         $transition;
}
@mixin translate($x, $y) {
  -webkit-transform: translate($x, $y);
  -ms-transform: translate($x, $y); // IE9+
  -o-transform: translate($x, $y);
  transform: translate($x, $y);
}
@mixin box-shadow($shadow...) {
  -webkit-box-shadow: $shadow; 
  -ms-transform: $shadow; // IE9+
  -o-transform: $shadow;
  box-shadow: $shadow;
}
@mixin flexbox() {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
}
@mixin border-end($value) {
	-webkit-border-end: $value;
	-moz-border-end: $value;
	border-inline-end: $value;
}
@mixin border-start($value) {
	-webkit-border-start: $value;
	-moz-border-start: $value;
	border-inline-start: $value;
}

/*Preview Panel*/
.preview-panel {
  position: fixed;
  top: 100px;
  right: 0;  
  z-index: 999;
  width: 0px;     
  @include border-radius(5px, 0, 0, 5px);
  @include transition(all 0.45s cubic-bezier(0.25, 0.46, 0.45, 0.94)); 
  @media (max-width: 767px) {
    display: none;
  }
  #pp-wrapper {
    width: 280px;
    @include translate(280px, 0);   
  } 
  .pp-toggle {    
    @include ab(42px, n, n, 0);
    @include border-radius(3px, 0, 0, 3px);
    width: 40px;
    height: 40px; 
    background: var(--body-background-color);   
    color: var(--body-general-color);
    border: 1px solid var(--body-border-color);
    @include border-end(none);
    cursor: pointer;
    text-align: center;
    line-height: 38px;
    display: flex;
    align-items: center;
    justify-content: center;
    svg path {
        fill: var(--body-general-color); 
    }
  }
  .style-main-title {
    background: #f0f0f0;
    border-bottom: 1px solid #ddd;
    padding: 10px;
    line-height: 22px;
    font-size: calc(var(--body-font-size) + 1px);
    text-align: center;
    color: #000000;
    @include border-radius(5px, 0, 0, 0);
  }
  .pp-box {    
    padding: 0 0 15px;
    &:not(:last-child) {
      border-bottom: 1px solid #ddd;
    }
    .box-heading {
      text-align: center;
      font-size: calc(var(--body-font-size) - 1px);
      line-height: 20px;
      padding: 10px;
      color: #000000;
    }
    .box-menu {
      position: relative;      
      padding: 0;
      margin: 0 auto; 
      width: 150px;
      > label {
        padding: 0 5px; 
        margin: 0;
        line-height: 22px;
        border: 1px solid #ddd;
        background: #f0f0f0;
        width: 100%;
        cursor: pointer;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: calc(var(--body-font-size) - 2px);
      } 
      ul {
        margin-top: -1px;
        list-style: none;        
        min-width: 130px;
        border: 1px solid #ddd;
        background: #f0f0f0;
        padding: 6px 0;
        visibility: hidden; 
        z-index: 2;  
        width: 100%;
        @include opacity(0);
        @include ab(100%, 0, n, n);       
        @include transition(all 0.35s ease 0s);
        @include translate(0, 10px);
        li {
          a {
            font-size: calc(var(--body-font-size) - 2px);
            line-height: 16px;
            padding: 2px 5px;
            display: block;
          }  
        }
      }      
    }  
    #box-colors {
      @include flexbox();
      flex-wrap: wrap;
      width: 100%;
      justify-content: center; 
      .changecolor {
        display: block;
        width: 20px;
        height: 20px;
        text-indent: -9999px;
        margin: 0 3px 3px 0;
        cursor: pointer;
        &.green {
          background-color: #a3d133;
        }
        &.pink {
          background-color: #ce1d76;
        }
        &.black {
          background-color: #000;
        }
        &.gold {
          background-color: #dab26d;
        }
        &.yellow {
          background-color: #fed700;
        }
        &.blue {
          background-color: #0787ea;
        }
        &.red {
          background-color: #f30b0b;
        }
        &.orange {
          background-color: #f89a20;
        }
        &.flat-blue {
          background-color: #00abc5;
        }
      }      
    }
    .box-menu-wrapper {
      &.opened {
        .box-menu {
          ul {
            visibility: visible;            
            @include opacity(1);
            @include translate(0, 0);
          }
        }
      }
    }
  } 
}
#box-version {
  @include flexbox;  
  flex-wrap: wrap;
  justify-content: center; 
  width: fit-content;
  background: #a9a6a6;
  border-radius: 50px; 
  padding: 3px; 
  .changecolor {
    cursor: pointer;
    padding: 5px 10px;
    border-radius: 50px; 
    font-weight: 700; 
    color: #383838; 
    transition: background 0.2s ease-in-out, color 0.2s ease-in-out;
    &.light {
      background: #ffffff; 
      color: #000000; 
    }
  }
}  
#dark {
  #box-version .changecolor {
    &.light {
      background: transparent;
      color: #383838; 
    }
    &.dark {
      background: #ffffff; 
      color: #000000;
    }
  }
}  
#shopify-section-preview-panel {
  &.opened {
    .preview-panel {      
      width: 280px;       
      background: #ffffff;
      border: 1px solid #ddd;
      @include border-end(none);    
      @include box-shadow(0 2px 9px 2px rgba(0,0,0,0.14));
    }
    #pp-wrapper {  
      @include translate(0, 0);    
      @media (max-width: 767px) {
        max-height: calc(100vh - 100px);  
        overflow-x: hidden;
        overflow-y: auto;
      } 
    }
    .pp-toggle {
      left: -40px;
    }
  }  
}

body[data-rtl="true"] {
  .preview-panel {
    left: 0;
    right: auto;
    @include border-radius(0px, 5px, 5px, 0px);
    #pp-wrapper {
        @include translate(-280px, 0);
    }  
    .pp-toggle {
        left: 0;
      	right: auto;
        @include border-radius(0px, 3px, 3px, 0px); 
    } 
    .pp-box .box-menu >label .demo-icon {
        left: 0;
      	right: auto;
    }   
  } 
  #shopify-section-preview-panel.opened .pp-toggle {
      left: auto;
      right: -40px;
  }    
}

